:root {
  --bgColor: #303030;
  --fontColor: white;
  --linkColor: #00d0ff;
}


[data-theme="dark"] {
  --bgColor: #303030;
}


body {
  margin: 0;
  padding: 0;

  font-family: Arial, Helvetica, sans-serif;

  background-color: var(--bgColor);
  color: var(--fontColor);
}

a {
  color: var(--linkColor);
}